import Vue from 'vue'
import App from './App.vue'

import VueRouter from 'vue-router'
import VueTextareaAutosize from 'vue-textarea-autosize'
Vue.use(VueRouter)
Vue.use(VueTextareaAutosize)

import AppNavbar from './components/Navbar'
import AppFlowchart from './components/Flowchart'
import AppSpreadsheet from './components/Spreadsheet'
import AppDocumentation from './components/Documentation'

Vue.component('AppNavbar', AppNavbar);
Vue.component('AppFlowchart', AppFlowchart);
Vue.component('AppSpreadsheet', AppSpreadsheet);
Vue.component('AppDocumentation', AppDocumentation);

const router = new VueRouter({
    routes: [
        { path: '/', component: AppFlowchart},
        { path: '/spreadsheet', component: AppSpreadsheet },
        { path: '/documentation', component: AppDocumentation }
    ],
    linkExactActiveClass: 'is-active'
})


function makePort(name, spot, output, input) {
    // the port is basically just a small circle that has a white stroke when it is made visible
    return $(go.Shape, "Circle",
             {
                fill: "transparent",
                stroke: null,
                desiredSize: new go.Size(8, 8),
                alignment: spot, alignmentFocus: spot,
                portId: name,
                fromSpot: spot, toSpot: spot,
                fromLinkable: output, toLinkable: input,
                cursor: "pointer"
             });
  };

function nodeStyle() {
    return [
      new go.Binding("location", "loc", go.Point.parse).makeTwoWay(go.Point.stringify),
      {
        locationSpot: go.Spot.Center,

        mouseEnter: function (e, obj) { showPorts(obj.part, true); },
        mouseLeave: function (e, obj) { showPorts(obj.part, false); }
      }
    ];
};

function showPorts(node, show) {
    var diagram = node.diagram;
    if (!diagram || diagram.isReadOnly || !diagram.allowLink) return;
    node.ports.each(function(port) {
        port.stroke = (show ? "white" : null);
      });
};

function showLinkLabel(e) {
    var label = e.subject.findObject("LABEL");
    if (label !== null) label.visible = (e.subject.fromNode.data.figure === "Diamond");
};

var lightText = 'whitesmoke';
var $ = go.GraphObject.make;
Vue.component("diagram", {
    template: "<div></div>",
    props: ["modelData"],
    data: function() {
        return {
            diagram: null
        }
    },
    mounted: function() {
        var self = this;
        var myDiagram =
        $(go.Diagram, "myDiagramDiv",
        {
          initialContentAlignment: go.Spot.Center,
          allowDrop: true,
          "LinkDrawn": showLinkLabel,
          "LinkRelinked": showLinkLabel,
          scrollsPageOnFocus: false,
          "undoManager.isEnabled": true,
          "ModelChanged": function(e) { self.$emit("model-changed", e); },
          "ChangedSelection": function(e) { self.$emit("changed-selection", e); },
          "animationManager.isEnabled": false
        });
        myDiagram.nodeTemplateMap.add ("",
            $(go.Node, "Spot", nodeStyle(),
                // the main object is a Panel that surrounds a TextBlock with a rectangular Shape
                $(go.Panel, "Auto",
                $(go.Shape, "Rectangle",
                    { fill: "#94a0b2", stroke: null },
                    new go.Binding("figure", "figure")),
                $(go.TextBlock,
                    {
                    font: "font-size: 1rem",
                    stroke: lightText,
                    margin: 8,
                    maxSize: new go.Size(100, NaN),
                    wrap: go.TextBlock.WrapFit,
                    editable: true
                    },
                    new go.Binding("text").makeTwoWay())
                ),
                // four named ports, one on each side:
                makePort("T", go.Spot.Top, false, true),
                makePort("L", go.Spot.Left, true, true),
                makePort("R", go.Spot.Right, true, true),
                makePort("B", go.Spot.Bottom, true, false)
            ));
            myDiagram.nodeTemplateMap.add ("Start",
            $(go.Node, "Spot", nodeStyle(),
                $(go.Panel, "Auto",
                $(go.Shape, "Ellipse",
                    { minSize: new go.Size(80, 40), fill: "#26A65B", stroke: null }),
                $(go.TextBlock, "Start",
                    {stroke: lightText },
                    new go.Binding("text"))
                ),
                // three named ports, one on each side except the top, all output only:
                makePort("L", go.Spot.Left, true, false),
                makePort("R", go.Spot.Right, true, false),
                makePort("B", go.Spot.Bottom, true, false)
            )
            );
            myDiagram.nodeTemplateMap.add ("End",
            $(go.Node, "Spot", nodeStyle(),
                $(go.Panel, "Auto",
                $(go.Shape, "Ellipse",
                    { minSize: new go.Size(80, 40), fill: "#EF4836", stroke: null }),
                $(go.TextBlock, "End",
                    { stroke: lightText },
                    new go.Binding("text"))
                ),
                // three named ports, one on each side except the bottom, all input only:
                makePort("T", go.Spot.Top, false, true),
                makePort("L", go.Spot.Left, false, true),
                makePort("R", go.Spot.Right, false, true)
            )
            );
            // myPalette =
            // $(go.Palette, "myPaletteDiv", 
            //     {
            //     scrollsPageOnFocus: false,
            //     nodeTemplateMap: myDiagram.nodeTemplateMap,  // share the templates used by myDiagram
            //     model: new go.GraphLinksModel([  // specify the contents of the Palette
            //         { category: "Start", text: "Start" },
            //         { text: "Step" },
            //         { text: "???", figure: "Diamond" },
            //         { category: "End", text: "End" },
            //         { category: "Comment", text: "Comment" }
            //     ])
            // });
        myDiagram.linkTemplate =
        $(go.Link,  // the whole link panel
            {
              routing: go.Link.AvoidsNodes,
              curve: go.Link.JumpOver,
              corner: 5, toShortLength: 4,
              relinkableFrom: true,
              relinkableTo: true,
              reshapable: true,
              resegmentable: true,
              // mouse-overs subtly highlight links:
              mouseEnter: function(e, link) { link.findObject("HIGHLIGHT").stroke = "rgba(30,144,255,0.2)"; },
              mouseLeave: function(e, link) { link.findObject("HIGHLIGHT").stroke = "transparent"; }
            },
            new go.Binding("points").makeTwoWay(),
            $(go.Shape,  // the highlight shape, normally transparent
              { isPanelMain: true, strokeWidth: 4, stroke: "transparent", name: "HIGHLIGHT" }),
            $(go.Shape,  // the link path shape
              { isPanelMain: true, stroke: "gray", strokeWidth: 2 }),
            $(go.Shape,  // the arrowhead
              { toArrow: "standard", stroke: null, fill: "gray"}),
            $(go.Panel, "Auto",  // the link label, normally not visible
              { visible: false, name: "LABEL", segmentIndex: 2, segmentFraction: 0.5},
              new go.Binding("visible", "visible").makeTwoWay(),
              $(go.Shape, "RoundedRectangle",  // the label shape
                { fill: "#FFFFFF", stroke: null }),
              $(go.TextBlock, "Yes",  // the label
                {
                  textAlign: "center",
                  font: "font-size: 0.5rem",
                  stroke: "#333333",
                  editable: true
                },
                new go.Binding("text").makeTwoWay())
            )
          );
        this.diagram = myDiagram;
        this.updateModel(this.modelData);
    },
    watch: {
        modelData: function(val) {
            this.updateModel(val);
        }
    },
    methods: {
        model: function() {
            return this.diagram.model;
        },
        updateModel: function(val) {
            if (val instanceof go.Model) {
                this.diagram.model = val;
            } else {
                var m = new go.GraphLinksModel()
                if (val) {
                    for (var p in val) {
                        m[p] = val[p];
                    }
                }
                this.diagram.model = m;
            }
        },
        updateDiagramFromData: function() {
            this.diagram.startTransaction();
            this.diagram.updateAllRelationshipsFromData();
            this.diagram.updateAllTargetBindings();
            this.diagram.commitTransaction("updated");
        }
    }
})

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



