<template>
<div class="container" id="diagram-content">
    <div style="width: 100%; justify-content: space-between">
      <!-- <div id="myPaletteDiv" 
            
            style="width: 100px; margin-right: 2px; background-color: whitesmoke; border: solid 1px black"></div> -->
      <diagram ref="diag" id="myDiagramDiv"
            v-bind:model-data="diagramData"
            style="background-color: #fff; flex-grow: 1; width:100%; height: 70vh"></diagram>
    </div>
    <!-- <diagram ref="diag"
             v-bind:model-data="diagramData"
             v-on:model-changed="modelChanged"
             v-on:changed-selection="changedSelection"
             style="background-color: #fff; width:100%; height:400px"></diagram> -->
    <!-- <button v-on:click="addNode">Add Child to Gamma</button>
    <button v-on:click="modifyStuff">Modify view model data without undo</button>
    <br/>Current Node:
    <input v-model.lazy="currentNodeText" v-bind:disabled="currentNode === null"/>
    <br/>The saved GoJS Model:
    <textarea style="width:100%;height:250px" v-model="savedModelText"></textarea>  -->
</div>
</template>

<script>

export default {
    data() {
      return{
        diagramData: {  // passed to <diagram> as its modelData
          nodeDataArray: [
            {"key":-1, "category":"Start", "loc":"-360 50", "text":"Start"},
            {"key":0, "loc":"-250 50", "text":"Log on to\nACME Bank\nwebsite and\nfill application"},
            {"key":1, "loc":"-250 150", "text":"Receive\nvendor\napplication\nnotification"},
            {"key":2, "loc":"-115 150", "text":"Review\napplication\nfor adequately\nand validation\nof supporting\ndocumentation"},
            {"key":3, "loc":"55 150", "figure":"Diamond", "text":"application\nadequate?"},
            {"key":4, "loc":"227 150", "text":"Move Vendor\nto Inforsec\nreview querie"},
            {"key":5, "loc":"610 142", "text":"Decline vendor application to work with vendor to adress issues"},
            {"key":6, "loc":"742 142", "category":"End", "text":"End"},
            {"key":7, "loc":"227 250", "text":"Conduct\nPen Test\non vendor\nInformation\nSystem"},
            {"key":8, "loc":"361 250", "text":"Perform site\nvisit to\nassess vendor\nphysical\nsecurity"},
            {"key":9, "loc":"527 250", "figure":"Diamond", "text":"Vendor\nsecure?"},
            {"key":10, "loc":"300 378", "category":"End",  "text":"End"},
            {"key":11, "loc":"418 378", "text":"Update\nVendor\nstatus in\nvendor mgt\ndatabase"},
            {"key":12, "loc":"527 378", "text":"Grant access\ntospecific\ninformation\nsystems as\nrequired"}
          ],
          linkDataArray: [
            {"from":-1, "to":0, "fromPort":"R", "toPort":"L"},
            {"from":0, "to":1, "fromPort":"B", "toPort":"T"},
            {"from":1, "to":2, "fromPort":"L", "toPort":"R"},
            {"from":2, "to":3, "fromPort":"R", "toPort":"L"},
            {"from":3, "to":0, "fromPort":"T", "toPort":"R", "visible":true, "text":"No"},
            {"from":3, "to":4, "fromPort":"R", "toPort":"L", "visible":true, "text":"Yes"},
            {"from":4, "to":7, "fromPort":"B", "toPort":"T"},
            {"from":7, "to":8, "fromPort":"R", "toPort":"L"},
            {"from":8, "to":9, "fromPort":"B", "toPort":"T"},
            {"from":9, "to":5, "fromPort":"T", "toPort":"B", "visible":true, "text":"No"},
            {"from":5, "to":6, "fromPort":"R", "toPort":"L"},
            {"from":9, "to":12, "fromPort":"B", "toPort":"T", "visible":true, "text":"Yes"},
            {"from":12, "to":11, "fromPort":"L", "toPort":"R"},
            {"from":11, "to":10, "fromPort":"L", "toPort":"R"}
          ]
        },
        currentNode: null,
        savedModelText: "",
        counter: 1,  // used by addNode
        counter2: 4  // used by modifyStuff
      }
    },
    computed: {
      currentNodeText: {
        get: function() {
          var node = this.currentNode;
          if (node instanceof go.Node) {
            return node.data.text;
          } else {
            return "";
          }
        },
        set: function(val) {
          var node = this.currentNode;
          if (node instanceof go.Node) {
            var model = this.model();
            model.startTransaction();
            model.setDataProperty(node.data, "text", val);
            model.commitTransaction("edited text");
          }
        }
      }
    },
    methods: {
      model: function() { return this.$refs.diag.model(); },
      // updateDiagramFromData: function() { this.$refs.diag.updateDiagramFromData(); },
      // modelChanged: function(e) {
      //   if (e.isTransactionFinished) {
      //     this.savedModelText = e.model.toJson();
      //   }
      // },
      // changedSelection: function(e) {
      //   var node = e.diagram.selection.first();
      //   if (node instanceof go.Node) {
      //     this.currentNode = node;
      //     this.currentNodeText = node.data.text;
      //   } else {
      //     this.currentNode = null;
      //     this.currentNodeText = "";
      //   }
      // },
      // addNode: function() {
      //   var model = this.model();
      //   model.startTransaction();
      //   model.setDataProperty(model.findNodeDataForKey(4), "color", "purple");
      //   var data = { text: "NEW " + this.counter++, color: "yellow" };
      //   model.addNodeData(data);
      //   model.addLinkData({ from: 3, to: model.getKeyForNodeData(data) });
      //   model.commitTransaction("added Node and Link");
      //   // also manipulate the Diagram by changing its Diagram.selection collection
      //   var diagram = this.$refs.diag.diagram;
      //   diagram.select(diagram.findNodeForData(data));
      // },
      // modifyStuff: function() {
      //   var data = this.diagramData;
      //   data.nodeDataArray[0].color = "red";
      //   data.nodeDataArray.push({ key: ++this.counter2, text: this.counter2.toString(), color: "orange" });
      //   data.linkDataArray.push({ from: 2, to: this.counter2 });
      //   this.updateDiagramFromData();
      // }
    }
    
}
</script>

<style scoped>
#diagram-content {
  margin-top: 20px;
  min-width: 96vw;
}

</style>


